import * as React from "react"
import Layout from "../components/layout"
import { StaticImage } from "gatsby-plugin-image"
import { graphql } from "gatsby"
import PageTitle from "../components/PageTitle"
import ServiceBlock from "../components/ServiceBlock"
import Seo from "../components/seo"
import { faTruckField } from "@fortawesome/free-solid-svg-icons/faTruckField"
import { faPersonDigging } from "@fortawesome/free-solid-svg-icons/faPersonDigging"
import { faTrowel } from "@fortawesome/free-solid-svg-icons/faTrowel"

export const Head = () => <Seo />

let fa_icons = [faTrowel, faPersonDigging, faTruckField]

const ProductsPage = ({ data, pageContext }) => {
  return (
    <Layout>
      <PageTitle title="Productos" />
      <section
        className="elementor-section elementor-top-section elementor-element  elementor-section-full_width elementor-section-height-default elementor-section-height-default"
        data-element_type="section"
      >
        <div className="elementor-container elementor-column-gap-default">
          <div
            className="elementor-column elementor-col-100 elementor-top-column elementor-element"
            data-element_type="column"
          >
            <div className="elementor-widget-wrap elementor-element-populated">
              <div
                className="elementor-element  elementor-widget elementor-widget-montro_services_v4"
                data-element_type="widget"
                data-widget_type="montro_services_v4.default"
              >
                <div className="elementor-widget-container">
                  {/* <!-- Services Section Two --> */}
                  <section className="services-section-two">
                    <div className="pattern-layer pattern-18"></div>

                    <div className="side-icon">
                      <StaticImage
                        src="../images/content/service-icon.png"
                        alt="Awesome"
                      />
                    </div>
                    <div className="auto-container">
                      {/* <!-- Sec Title --> */}
                      <div className="sec-title centered">
                        <div className="title">
                          <span className="separator"></span>What We Do
                          <span className="separator-two"></span>
                        </div>
                        <h2>
                          We will satisfy you by our <br /> servicing plan
                        </h2>
                      </div>
                      <div className="row clearfix">
                        {data.productos.nodes.map((item, i) => {
                          return (
                            <ServiceBlock
                              title={item.title}
                              text={item.description}
                              icon={fa_icons[i]}
                              href={item.href}
                              image={item.image.childImageSharp}
                            />
                          )
                        })}
                      </div>
                    </div>
                  </section>
                  {/* <!-- End Services Section Two --> */}
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </Layout>
  )
}

export const query = graphql`
  query {
    productos: allProductosJson {
      nodes {
        title
        description
        href
        image {
          childImageSharp {
            gatsbyImageData
          }
        }
      }
    }
  }
`

export default ProductsPage
